@import url(https://fonts.googleapis.com/css?family=Seaweed+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{max-width:800px;margin:72px auto;padding:0 96px}@media screen and (max-width: 600px){.container.container{margin:-16px auto 0 auto;padding:0 32px}}.container .intro{min-height:6em}@media screen and (max-width: 600px){.container p{font-size:18px}}.container p span{display:inline-block;position:relative;font-weight:600}.container .project-image{box-sizing:border-box;width:50%;padding-bottom:calc(50% - 16px);height:0;background:#fef3f3;position:relative;-webkit-transition:box-shadow 0.3s;transition:box-shadow 0.3s}.container .project-image img{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:calc(100% - 128px);height:auto;border-radius:8px;margin:0}@media screen and (max-width: 600px){.container .project-image{width:100%;padding-bottom:100%}.container .project-image img{border-radius:8px}}.container .project{display:flex;margin:32px 0;color:black;text-decoration:none}.container .project:hover .project-image{box-shadow:0 4px 12px 0 rgba(0,0,0,0.25)}@media screen and (max-width: 600px){.container .project{flex-direction:column}}.container .project:first-of-type .project-image{background:#f7e6ff}.container .project:nth-of-type(3) .project-image img,.container .project:nth-of-type(4) .project-image img{width:100%;border-radius:0}.container .project-desc{padding-left:32px;width:50%;text-align:left}.container .project-desc h1{text-transform:uppercase;letter-spacing:0.5px;font-size:1.2em;font-weight:600;margin-bottom:0;text-align:left}.container .project-desc h2{font-size:0.5em;margin:0 0 auto 0;text-align:left}.container .project-desc p{font-size:0.75em}@media screen and (max-width: 600px){.container .project-desc{padding-left:0;width:100%;text-align:center}.container .project-desc h1{text-align:center}.container .project-desc h2{text-align:center}}

