.fade-enter{opacity:0.01;pointer-events:none;-webkit-transform:translateY(30px);transform:translateY(30px)}.fade-enter.fade-enter-active{opacity:1;pointer-events:auto;-webkit-transform:translateY(0);transform:translateY(0);-webkit-transition:all 400ms;transition:all 400ms}.fade-exit{opacity:0;position:fixed;pointer-events:auto}.fade-exit.fade-exit-active{opacity:0.01;pointer-events:none;position:fixed;-webkit-transition:opacity 400ms;transition:opacity 400ms}div.transition-group{position:relative}section.route-section{position:absolute;width:100%;top:0;left:0}

.header{position:fixed;top:0;left:0;right:0;padding:16px 32px;z-index:2;display:flex;justify-content:space-between;align-items:center}@media screen and (max-width: 600px){.header{position:relative}}.header .right{display:flex}.header .right svg{height:32px;width:32px;margin-left:16px}.header .right svg:hover{fill:rgba(0,0,0,0.7)}.title{color:black;font-family:"Seaweed Script", cursive;font-size:32px;text-decoration:none}.title:hover{color:rgba(0,0,0,0.7)}

